body {
  margin: 0; /* body의 기본 margin 제거 */
  padding: 0; /* body의 기본 padding 제거 */
  display: flex; /* body를 flex 컨테이너로 설정 */
  justify-content: center; /* 수평 가운데 정렬 */
  align-items: center; /* 수직 가운데 정렬 */
  height: 100vh; /* 화면 전체 높이로 설정 */
  /*background-color: rgba(0, 0, 0, 0.5); /* 반투명 검정 배경 */
  background-color: rgba(128, 128, 128, 0.5); /* 반투명 회색 배경 */

}
.App {
  text-align: center;
}

.App-header {
  background-color: rgba(128, 128, 128, 0.5); /* 반투명 회색 배경 */

  padding: 20px;
  text-align: center;
}

.App-main {
  padding: 20px;
}

.Login {
  background-color: rgba(128, 128, 128, 0.5); /* 반투명 회색 배경 */

  padding: 20px;
  border-radius: 10px;
  display: inline-block;
  margin-top: 100px;
  align-self: center;

}

.Login input {
  margin: 10px;
  padding: 10px;
  border: none;
  border-radius: 5px;
}

.Login button {
  background-color: #4CAF50; /* 녹색 배경 */
  color: white;
  padding: 15px 20px;
  margin: 10px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.Login button:hover {
  background-color: #45a049;
}

.File-operations {
  margin: auto;
  padding: 20px;
  max-width: 300px;
}

.File-list div {
  cursor: pointer;
  margin-bottom: 10px;
}

.File-list div:hover {
  text-decoration: underline;
}

/* ... 기존 스타일링 */

.File-item {
  cursor: pointer;
  margin-bottom: 10px;
}

.File-item.selected {
  color: blue;
  font-weight: bold;
}

.File-item:hover {
  text-decoration: underline;
}
